/* Earn.css */
.earn-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background: url('background_earn.jpg') no-repeat center center fixed;
  background-size: cover;
  color: #fff;
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
}

.case-container-wrapper {
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 15px;
  padding: 20px;
}

.case-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}

.case {
  width: 60px;
  height: 60px;
  background-size: cover;
  background-position: center;
  border: 2px solid #fff;
  border-radius: 10px;
  transition: transform 0.3s;
}

.case:hover {
  transform: scale(1.1);
}

.case-details-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.case-details {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  color: #000;
}

.case-details img {
  width: 60px; /* Same size as the case on the page */
  height: 60px; /* Same size as the case on the page */
}

.case-details button {
  margin-top: 10px;
  padding: 10px 20px;
  background-color: #4caf50;
  color: white;
  border: none;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
}

.battle-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 1000;
  color: white;
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
}

.battle-kiza {
  width: 60px; /* Same size as the case on the page */
  height: 60px; /* Same size as the case on the page */
  margin-bottom: 20px;
}

.battle-message {
  margin-bottom: 20px;
}

.messages-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.message {
  position: absolute;
  font-size: 20px;
  color: #fff;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  animation: fadeOut 2s forwards;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(-20px);
  }
}
